* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
@font-face {
  font-family: "PT Sans";
  font-weight: normal;
  font-style: normal;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  font-weight: bold;
  font-style: normal;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
}
.container {
  max-width: 1100px;
  margin: 0 auto;
}
.container--nav {
  display: grid;
  grid-template-columns: min-content 1fr;
  align-items: center;
}
.container--skills {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: min-content min-content 1fr;
  grid-row-gap: 20px;
  align-content: center;
  justify-self: center;
}
.container--about-us {
  display: grid;
  grid-template-columns: 1fr 1fr min-content;
  grid-template-rows: min-content min-content;
  grid-template-areas: "lf lf hs" "tf tf hs" "pl pl hs";
  grid-column-gap: 50px;
  grid-row-gap: 20px;
}
.body {
  min-height: 100vh;
  margin: 0;
  padding: 0;
  font-family: "PT Sans", "Arial", sans-serif;
  background-color: #ffffff;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: min-content 1fr min-content;
}
.body--scrolloff {
  overflow: hidden;
}
.header {
  width: 100%;
  height: min-content;
  background-color: transparent;
}
.header__wrap {
  width: 100%;
  z-index: 2;
  padding: 0 20px;
  position: fixed;
  color: #fff;
  background-color: rgba(0, 0, 0, 0.1);
  border-bottom: 1px solid #a1a1a1;
  transition: 0.5s all ease-in;
}
.header__wrap--scroll {
  background-color: rgba(255, 255, 255, 0.95);
  transition: 0.5s all ease-in;
  color: #000;
}
.main {
  width: 100%;
  height: 100%;
}
.footer {
  width: 100%;
  height: 55px;
}
.nav {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: repeat(6, auto);
  justify-content: end;
  grid-column-gap: 40px;
  background-color: transparent;
}
.nav__item {
  height: 100%;
  list-style: none;
}
.nav__link {
  width: 100%;
  height: 100%;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'Rubik', sans-serif;
  color: currentColor;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  text-decoration: none;
}
.nav__link--current {
  pointer-events: none;
}
.nav__link:hover::after,
.nav__link:focus::after,
.nav__link--current::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 107px;
  height: 2px;
  background-color: currentColor;
}
.cover {
  width: 100%;
  height: 100vh;
  min-height: 600px;
  padding: 80px 20px;
  background-image: url('../../images/cover.jpg');
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 40px;
}
.cover__title {
  font-family: 'Rubik', sans-serif;
  font-weight: 800;
  font-size: 48px;
  line-height: 64px;
  text-align: center;
  color: #ffffff;
}
.cover__link {
  width: 200px;
  height: 45px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #4985FF;
  box-shadow: 0px 0px 10px rgba(111, 111, 111, 0.25);
  border-radius: 30px;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  color: #ffffff;
  text-decoration: none;
}
.liquid-button {
  font-weight: bold;
}
.liquid-button path {
  cursor: pointer;
}
.skills {
  width: 100%;
  padding: 100px 20px;
}
.skills__title {
  font-family: 'Rubik', sans-serif;
  font-weight: bold;
  font-size: 36px;
  line-height: 53px;
  text-align: center;
  color: #323232;
}
.skills__subtitle {
  font-family: 'Rubik', sans-serif;
  font-weight: normal;
  font-size: 16px;
  line-height: 25px;
  text-align: center;
  color: #999999;
}
.skills__list {
  width: 100%;
  padding: 50px 0 0 0;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  gap: 50px;
}
.skills__item {
  width: 200px;
  position: relative;
  list-style: none;
  display: grid;
  grid-template-columns: min-content 1fr;
  grid-template-rows: repeat(2, min-content);
  grid-column-gap: 15px;
  grid-row-gap: 5px;
}
.skills__skill {
  font-family: 'Rubik', sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 22px;
  color: #000000;
}
.skills__text {
  font-family: 'Rubik', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 22px;
  color: #999999;
  grid-column-start: 1;
  grid-column-end: -1;
}
.about-us {
  width: 100%;
  padding: 100px 20px;
  background: #F6F6F8;
}
.about-us__info {
  grid-area: lf;
  height: min-content;
  display: grid;
  grid-template-rows: min-content min-content;
  grid-row-gap: 20px;
}
.about-us__history {
  grid-area: hs;
  display: grid;
  grid-template-rows: repeat(3, min-content);
  grid-row-gap: 20px;
  padding: 40px 20px;
  background-color: #0068ab;
  color: #ffffff;
  box-shadow: 14px 14px 28px #929292, -14px -14px 28px #f5f5f5;
  border-radius: 30px;
}
.about-us__mission {
  grid-area: tf;
}
.about-us__politic {
  grid-area: pl;
}
.about-us__title {
  font-family: 'Rubik', sans-serif;
  font-weight: bold;
  font-size: 36px;
  line-height: 53px;
  color: #323232;
}
.about-us__title--history {
  color: #ffffff;
}
.about-us__text {
  font-family: 'Rubik', sans-serif;
  font-size: 16px;
  line-height: 25px;
}
.about-us__img {
  width: 200px;
  height: 200px;
  background-color: #929292;
}
.about-us__quotes {
  font-family: 'Open Sans', sans-serif;
  font-weight: 300;
}
.about-us__quotes-autor {
  font-family: 'Open Sans', sans-serif;
  text-transform: initial;
  display: block ;
  margin-top: 10px;
}
.mission-list {
  padding-left: 20px;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: min-content;
  grid-row-gap: 10px;
}
.mission-list__item {
  font-family: 'Rubik', sans-serif;
}
